@import 'variables';

*, ::after, ::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    padding: 0;
    margin: 0;
    background: $white-color;
    font-family: "Ubuntu", sans-serif;
}

p {
    margin-bottom: 0;
}

img {
    border: none;
}

header, footer {
    margin: auto;
    max-width: 1920px;
}

main {
    margin: auto;
}

a {
    text-decoration: none;
    cursor: pointer;
}

button {
    border: none;
    cursor: pointer;
}

ul {
    list-style: none;
}


.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.order {
    &__header {
        margin-bottom: 50px;
        font-family: inherit;
        font-weight: 600;
        font-size: 28px;
        line-height: 1;
        color: #000;
    }

    &__layout {
        margin-bottom: 40px;
       display: flex; 
       gap: 30px;
    }

    &__left {
        width: 100%;
        counter-reset: step; 
    }

    &__right {
        display: none;
        width: 370px;
    }

    &__tab {
       
        width: 43vw;
        height: 53px;
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        text-transform: uppercase;
        text-align: center;
        color: #000;
        background: none;
        border: 1px solid #EEEEEE;
    }

}

.tabs {

    &__nav {
        margin-bottom: -1.2px;
        display: flex;
        gap: 10px;
        

    }

    &__nav-btn {
        background: #FFFFFF;
        border-bottom: 1px solid #eeeeee;
        &_active {
            // border-bottom: none;
            border-bottom: 1px solid #ffffff;
        }
    }

    &__panel {
        display: none;
        width: 100%;
        border: 1px solid #EEEEEE;

        &_active {
            display: block;
            
        }

    }
   


}

.total-cost {
    position: sticky;
    top: 0;
    border: 1px solid #EEEEEE;

    &__title {
        padding: 20px;
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        text-transform: uppercase;
        text-align: center;
        color: #000;
        border-bottom: 1px solid #EEEEEE;
        text-align: left;
    }

    &__info {
        padding: 20px;
        border-top: 1px solid #EEEEEE;
        border-bottom: 1px solid #EEEEEE;
        font-weight: 400;
        
        line-height: 150%;
        color: #000;
    }

    &__total-sum {
        padding: 20px 20px 21px 20px;
        color: #a91d2d;

        &_num {
            font-family: inherit;
            font-weight: 700;
            font-size: 16px;
            line-height: 150%;
        }

        &_currency {
            font-family: "Roboto", sans-serif;
            font-weight: 600;
        }
    }

    &__count, &__weight {
        margin-bottom: 10px;
    }

    &__property {
        display: inline-block;
        width: 104px;
        font-size: 16px;

        &_sum {
            font-family: "Roboto", sans-serif;
            font-weight: 500;
            font-style: normal;
            font-size: 14px;
            line-height: 150%;
            color: #000;
        }
    }

    &__value, &__measure, &__currency {
        font-size: 14px;
    }
     
}

.step {

    padding: 25px 15px;
    border-bottom: 1px solid #eee;
   
    &__checkbox-label {
        display: block;
        // min-width: 100px;
        border: 1px solid #eee;
        width: 100%;
    }

    &__checkbox-text {
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: #000;
    }

    &__second-title {
        margin-bottom: 10px;
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        color: #000;
    }

    &__title {
        margin-bottom: 20px;
        font-family: "Ubuntu", sans-serif;
        font-weight: 500;
        font-size: 18px;
        line-height: 150%;
        color: #000;
          

        &::before {
            counter-increment: step;
            content: counter(step);
            display: inline-flex;
            justify-content: center;
            align-items: center;
            margin-right: 10px;
            width: 28px;
            height: 28px;
            background:#A91D2D;
            border-radius: 50%;
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            text-align: center;
            color: #fff;
        }
    }

    &__input {
        padding: 12px 15px;
        // margin-bottom: 15px;
        border: 1px solid #eee;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: #000;
    }

    &__height {
        margin-bottom: 15px;
        font-family: "Ubuntu", sans-serif;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 10px;

        &_note {
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;
            color: #7c7c7c;
        }

    }

    #height-num {
        width: 71px;

        &::placeholder {
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            color: #c6c6c6;
        }
    }

    &__receiver {
        margin-bottom: 15px;
        display: flex;
        flex-wrap: wrap;
        gap: 15px;

        &_name {
            width: 100%;
        } 
    }

    &__textarea {
        width: 100%;
        resize: none;
        height: 90px;

        &::placeholder {
            font-family: "Ubuntu", sans-serif;
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            color: #c6c6c6;
        }
    }

    &__dates {
        display: flex;
        flex-wrap: wrap;
    }




}

.checkbox {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #7c7c7c;
    cursor: pointer;
   
    padding: 12px 15px;
    

    &__input {
        display: none;
    }

    &__text {
        position: relative;
        padding-left: 26px;
        display: inline-block;
        
        transition: all .3s ease-in-out;

        &::before {
            content: '';
            position: absolute;
            display: inline-block;
            top: 0;
            left: 0;
            width: 18px;
            height: 18px;
            border: 1px solid #eaeaea;

            transition: all .3s ease-in-out;
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            transform: translate(64%, 64%);
            height: 8px;
            width: 8px;
            background: #A91D2D;
            opacity: 0;
            transition: all .3s ease-in-out;
        }

    }
    &__input:checked + &__text::before {
        border-color: #A91D2D;
    }

    &__input:checked + &__text::after {
        opacity: 1;
    }


 
}

.radio {
    &__input {
        display: none;
    }

    &__dates {
        width: 86px;
        text-align: center;
    }

    &__day, &__month {
        font-weight: 400;
        font-size: 12px;
        line-height: 150%;
        text-transform: uppercase;
        color: #000;
    }

    &__num {
        font-weight: 500;
        font-size: 18px;
        line-height: 150%;
        text-align: center;
        color: #000;
    }


    &__block {
        position: relative;
        padding-top: 40px;
        padding-bottom: 12px;
        width: 100%;
        display: inline-block;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: #000;
        border: 1px solid #eee;
        transition: all .3s ease-in-out;

        &::before {
            content: '';
            position: absolute;
            display: inline-block;
            top: 12px;
            left: 50%;
            transform: translateX(-50%);
            width: 18px;
            height: 18px;
            border: 1px solid #eaeaea;
            border-radius: 50%;
            transition: all .3s ease-in-out;
        }

        &::after {
            content: '';
            position: absolute;
            top: 12px;
            left: 50%;
            transform: translate(-50%, 64%);
            height: 8px;
            width: 8px;
            background: #A91D2D;
            opacity: 0;
            border-radius: 50%;
            transition: all .3s ease-in-out;
        }
    }

    &__input:checked + &__block::before {
        border-color: #A91D2D;
    }

    &__input:checked + &__block::after {
        opacity: 1;
    }

    &__input:checked + &__block {
        border-color: #A91D2D;
    }
}


.time {

    .radio:not(:last-child) {
        display: block;
        margin-bottom: 15px;
    }

    &__input {
        display: none;
    }

    &__text {
        display: inline-block;
        padding-right: 6px;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: #7c7c7c;
    }

    &__sum {
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        color: #7c7c7c;
    }

    &__currency {
        font-family: "Roboto", sans-serif;
        font-size: 14px;
        line-height: 150%;
        color: #7c7c7c;
    }

    &__cost {
        margin-left: auto;
        display: inline-block;
    }

    &__info-header {
        width: 100%;
        display: flex;
        align-items: center;
    }

    &__info {
        position: relative;
        padding: 12px 15px 12px 43px;
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        border: 1px solid #eee;
        width: 100%;
        border: 1px solid #eee;
        transition: all .3s ease-in-out;

        &::before {
            content: '';
            position: absolute;
            display: inline-block;
            // top: 12px;
            left: 15px;
            // transform: translateX(-50%);
            width: 18px;
            height: 18px;
            border: 1px solid #eaeaea;
            border-radius: 50%;
            transition: all .3s ease-in-out;
        }

        &::after {
            content: '';
            position: absolute;
            left: 20px;
            top: 17px;
            height: 8px;
            width: 8px;
            background: #A91D2D;
            opacity: 0;
            border-radius: 50%;
            transition: all .3s ease-in-out;
        }

        &_selector {
            flex-direction: column;
        }
    }

    &__input:checked + &__info::before {
        border-color: #A91D2D;
    }

    &__input:checked + &__info::after {
        opacity: 1;
    }

    &__input:checked + &__info {
        background: #FAFAFB;
        border: none;
        .time__text {
            // font-weight: 400;
            // font-size: 14px;
            // line-height: 150%;
            color: #000;
        }

        .time__sum, .time__currency {
            color: #A91D2D;
        }


    }

    &__info-header {
        margin-bottom: 10px;
    }

    &__selector-block {
        display: flex;
        flex-wrap: wrap;
        gap: 15px;
    }

    &__selector-label {
        max-width: 179px;
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: #7c7c7c; 
    }

    &__selector {
        width: 200px;
        height: 45px;
        background: #ffffff;
        border: 1px solid #eee;
    }

    
}

.service {
