        margin-bottom: 10px;
        // margin-left: 25px;
        display: block;
        width: 100%;
        resize: none;
        background: #ffffff;
        border: 1px solid #eee;
        // width: 624px;
        height: 90px;
    }

    &__specialist {
        margin-bottom: 15px;
        // margin-left: 25px;
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: #000;
    }

    &__rights {
        // margin-left: 25px;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        color: #7c7c7c;
    }
}





.corsik_yaDeliveryMap {
