//media-queries
@media (min-width: 768px) {
    .container {
        width: 750px
    }

    .step {
        padding: 25px 30px;

        &__height {
            margin-bottom: 0;
        }

        &__checkbox-label {
            display: block;
            min-width: 435px;
            // border: 1px solid #eee;
        }

        &__receiver {
            &_name {
                width: 495px;
            }
        }
    }

    .radio {
        &__dates {
            width: 100px;
        }
    }

    .order {
        &__tab {
            width: 270px;
            height: 54px;
        }

        &__layout {
            margin-bottom: 80px;
        }
    }

    .total {
        &__btn {
            width: 228px;
        }
    }

   

}

@media (min-width: 992px) {
    .container {
        width: 970px
    }
}

@media (min-width: 1230px) {
    .container {
        width: 1200px
    }

    .tabs {
        &__nav {
            gap: 18px;
        }
    }

    .order {

        &__left {
            width: 770px;
        }
       
        &__right {
            display: block;
        }

      
    }

    .corsik_yaDeliveryMap {
        &__map {
            width: 710px;
            height: 350px;
        }
    }

   
}